mark{
    background-color: #93b9ae;
}
li{
    margin-top: 10px;
}


.page{
    position: absolute;
    left: 0px;
    right:0px;
    top: 0px;
    
}
.fade-appear,.fade-enter{
    opacity: 0;
    z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active{
opacity: 1;
transition: opacity 500ms linear 250ms;
}

.fade-exit{
    opacity: 1;
}

.fade-exit.fade-exit-active{
    opacity: 0;
    transition: opacity 200ms linear;

}

.certificate-img {
  position: relative;
  left: 10%;
    border-radius: 0.5rem;
    border: 2px ridge hsl(286, 52%, 55%);
    width: 80%;
    height: 80%;
    margin-top: 20px;
    margin-bottom: 40px;
    box-shadow: 0px 0px 10px 5px rgba(231, 236, 236, 0.856);
   
  }

  .certificate-img2 {
    position: relative;
    left: 22%;
      border-radius: 0.5rem;
      border: 2px ridge hsl(286deg 1% 8%);
      width: 55%;
      height: 55%;
      margin-top: 20px;
      margin-bottom: 40px;
      box-shadow: 0px 0px 10px 5px rgba(231, 236, 236, 0.856);
    }
.pagination-color{
  color: #fff;
  font-size: 1.2rem;
}

  .project-switch {
    color: hsl(103, 39%, 65%);
    font-size: 1.05rem;
    font-weight: 900;
    
  }
  
  .project-switch:hover {
    /* color: hsl(176, 74%, 28%); */
    color: hsl(103, 39%, 65%);
    font-size: 1rem;
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.3s ease-in-out;
    box-shadow: 0px 0px 10px 5px hsl(103, 39%, 65%);
  }

  
  .modal-logo {
    font-size: 1.25rem;
    padding-right: 0.25rem;
    color: #88b377;
  }

  .modal-close {
    font-size: 1.5rem;
    margin-left: 20rem;
    color: rgb(196, 44, 44);
  }
  
  .modal-close:hover {
    color: #88b377;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.3s;
    cursor: pointer;
  }

  .soft-icon-check{
    color: rgb(64, 197, 59);
    margin-right: 0.5rem;
  }

  .button-pdf{
    text-decoration: none;
    color:rgb(102, 185, 99);
    font-weight: 900;
    font-size: 1.05rem;
  }
  .resume-close{
    color: rgb(226, 77, 77);
    border-radius: 0.5rem;
    font-size: 2rem;
    margin-top: 20px;
    
    
  }
  .resume-close:hover{
    color: #88b377;
    transition:ease-in-out
  }
body{
  background-color: black;
}
.container {
background-color: #555BFF;
background-color: var(--primary);
display: flex;
padding: 40px;
max-width: 40vw;
/* min-height: 90px; */
justify-content: center;
align-self: center;
border: 3px solid #555BFF;
border: 3px solid var(--primary);
padding: 0;
margin-top: 10%;
margin-bottom: 10%;

box-shadow: 10px -10px 0 -3px rgba(255, 255, 255, 0.014), 10px -10px #1FC11B,
            20px -20px 0 -3px rgba(255, 255, 255, 0.014), 20px -20px #FFD913, 
            30px -30px 0 -3px rgba(255, 255, 255, 0.014), 30px -30px #FF9C55, 
            40px -40px 0 -3px rgba(255, 255, 255, 0.014), 40px -40px #FF5555;

box-shadow: 10px -10px 0 -3px var(--background), 10px -10px var(--green),
            20px -20px 0 -3px var(--background), 20px -20px var(--yellow), 
            30px -30px 0 -3px var(--background), 30px -30px var(--orange), 
            40px -40px 0 -3px var(--background), 40px -40px var(--red); 
transition: box-shadow 1s, top 1s, left 1s;
position: relative;
top: 0;
left: 0;
cursor: pointer;
color: white;
font-size: 1.2rem;

}

@media screen and (max-width: 800px){
  .container{
    max-width: 60vw;
    left: -20px;

  }
}

.container:hover {
top: -20px;
left: 20px;
box-shadow: 0 0 0 -3px rgba(255, 255, 255, 0.014), 0 0 0 0 #1FC11B,
    0 0 0 -3px rgba(255, 255, 255, 0.014), 0 0 0 0  #FFD913,
    0 0 0 -3px rgba(255, 255, 255, 0.014), 0 0 0 0  #FF9C55,
    0 0 0 -3px  rgba(255, 255, 255, 0.014), 0 0 0 0  #FF5555;
box-shadow: 0 0 0 -3px var(--background), 0 0 0 0 var(--green),
    0 0 0 -3px var(--background), 0 0 0 0  var(--yellow),
    0 0 0 -3px var(--background), 0 0 0 0  var(--orange),
    0 0 0 -3px  var(--background), 0 0 0 0  var(--red);
}

.input-fields{
max-width: 20vh;
height: 2vh;
}
.text-field{
max-width: 20vh;
height: 4vh;
}

:root {
--primary: #555BFF;
--secondary: #22D2A0;
--background: rgba(255, 255, 255, 0.014);
--background2: rgba(49, 48, 48, 0.466);
--green: #1FC11B;
--yellow: #FFD913;
--orange: #FF9C55;
--red: #FF5555;
} 

.form{
margin-top: 0;
margin-bottom: 0;
top: 10%;
position: relative;
font-weight: 900;

}

@media screen and (max-width: 800px){
  .form{
    
    left: -30px;
    
  }
}


